body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card {
  background: #fff;
  border: none;
  height: 100%;
  margin: 0.3rem;
  position: relative;
  width: 18%;
  text-align: left;
  cursor: pointer;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
}
.card > .img-container > img {
  width: 100%;
}

.card :hover {
  box-shadow: 0 3px 15px rgba(153, 153, 153, 0.616),
    0 3px 15px rgba(153, 153, 153, 0.486);
  border-radius: 50%;
}

.wrapper {
  padding-top: 50px;
  background: white;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}
.bg {
  background-image: url("https://s3.amazonaws.com/ksemal.com/img/bg.jpg?fbclid=IwAR0U-0d60_P-ci6AIcaTJxkTfdn63cXT12uSbLGJkFU1Aec9p5VNpEYXVSQ");
  background-size: cover;
  text-align: center;
  padding: 200px 250px 200px 250px;
  border-bottom: 4px solid rgba(128, 128, 128, 0.486);
  color: white;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-weight: bold;
  font-size: 3rem;
}
.bg > h2 {
  font-weight: bold;
}
.container-fluid {
  margin: 0;
  padding: 0;
}
.navbar {
  height: 4rem;
  border-bottom: 4px solid rgba(128, 128, 128, 0.295);
  color: rgba(28, 71, 50, 0.747);
  font-size: 1.2rem;
  font-weight: bold;
}
.title {
  font-weight: bold;
  color: rgba(27, 68, 48, 0.747);
  font-size: 1.5rem;
}
a:hover {
  color: rgba(27, 68, 48, 0.747);
  text-decoration: none;
}
.footer {
  height: 50px;
  background-color: rgba(230, 230, 230, 0.658);
  border-top: 4px solid rgba(128, 128, 128, 0.295);
  text-align: center;
  padding-top: 10px;
  font-size: 1.2rem;
}
.footer > a {
  color: rgba(54, 54, 54, 0.767);
}
.correct {
  -webkit-animation-name: colorchangeCorrect;
          animation-name: colorchangeCorrect;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
}

.incorrect {
  -webkit-animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
          animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
}
@-webkit-keyframes colorchangeCorrect {
  0% {
    color: rgba(28, 71, 50, 0.747);
  }
  25% {
    color: rgba(101, 228, 167, 0.911);
  }
  50% {
    color: rgba(28, 71, 50, 0.747);
  }
  75% {
    color: rgba(101, 228, 167, 0.911);
  }
  100% {
    color: rgba(28, 71, 50, 0.747);
  }
}
@keyframes colorchangeCorrect {
  0% {
    color: rgba(28, 71, 50, 0.747);
  }
  25% {
    color: rgba(101, 228, 167, 0.911);
  }
  50% {
    color: rgba(28, 71, 50, 0.747);
  }
  75% {
    color: rgba(101, 228, 167, 0.911);
  }
  100% {
    color: rgba(28, 71, 50, 0.747);
  }
}

@-webkit-keyframes shake {
  10%,
  90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }

  20%,
  80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
    color: rgba(192, 94, 37, 0.979);
  }

  30%,
  50%,
  70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%,
  60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
    color: rgba(192, 94, 37, 0.979);
  }
}

@keyframes shake {
  10%,
  90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }

  20%,
  80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
    color: rgba(192, 94, 37, 0.979);
  }

  30%,
  50%,
  70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%,
  60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
    color: rgba(192, 94, 37, 0.979);
  }
}

